import { Box } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Footer from '../components/Footer';
import TopContact from '../components/TopContact';
import Topbar from '../components/Topbar';
import AboutLanding from '../components/aboutPageComponents/AboutLanding';
import Mission from '../components/aboutPageComponents/Mission';
import MeetTheTeam from '../components/aboutPageComponents/MeetTheTeam';
import { useEffect } from 'react';

// #00a693 (a dark greenish-blue)
// #45b1e8 (a bright sky blue)
// #ff9900 (a bold orange)
// #ff4d4d (a vibrant red)

// Light Gray (#F5F5F5) 
// Beige (#F5F5DC)
// Light Blue (#E6F3F7) 

// original colors
// 00a99d (PANTONE 3272)  // greenish
// f36523 (PANTONE 171)   // orange
// f70809 (PANTONE 485)   // red
// 3fa9f5 (PANTONE 279 C) // blue


const theme = createTheme({
  palette: {
    primary: {
      main: '#00a99d',
      light: '#f2f2f2',
      dark: '#eeeeee',
    },
    secondary: {
      main: '#00a99d',
    },
  },
  typography: {
    fontFamily: 'Roboto',
    h1: {
      color: '#eeeeee',
      fontSize: '4rem',
      '@media (max-width: 768px)': {
        fontSize: '10vw',
      },
    },
    h2: {
      fontSize: '3.25rem',
      color: '#000000',
    },
    h3: {
      fontSize: '2.5rem',
      color: '#555555',
    },
    h4: {
      fontSize: '1.75rem',
      color: '#00a99d',
    },
    h5: {
      fontSize: '1rem',
      color: '#00a99d',
    },
    h6: {
      color: '#eeeeee',
      fontSize: '1.1rem',

    },
    body1: {
      color: '#777777',
      fontSize: '2.3vh'
    },
  },
});

export default function AboutPage() {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ backgroundColor: theme.palette.primary.main }}>
        <TopContact />
        <Topbar />
        <AboutLanding />
        <Mission />
        <MeetTheTeam />
        <Footer />
      </Box>
    </ThemeProvider>
  );
}